<template>
    <div class="html">
        <div class="container">
            <h2 class="h2">风格介绍</h2>
            <ul>
                <li></li>
            </ul>
            <div class="view" v-html="datahtml"></div>
        </div>
    </div>
</template>

<script>
import reuqest from "@/utils/request";
export default {
    data() {
        return {
			datahtml:''
		};
    },
    created() {
        this.getdata();
    },
    mounted() {},
    methods: {
        getdata() {
            reuqest({
                url: "/api/getArticleList",
                method: "post",
                data: {
                    type: "style"
                }
            }).then(data => {
				// console.log(data);
				this.datahtml =data.data.data[0].content;
            });
        }
    }
};
</script>

<style scoped>
p{
	margin-bottom: 10px;
}
.html {
    background: #e6e6e6;
}
.container {
    display: inline-block;
    width: 1200px;
    background: white;
    margin: 0 auto;
    padding: 0 50px 20px;
}
.h2 {
    font-size: 20px;
    padding-bottom: 10px;
    color: #333;
    border-bottom: 1px solid #999;
    margin-bottom: 20px;
    margin-top: 18px;
    text-align: left;
}
.view {
    text-align: left;
    color: rgb(51, 51, 51);
    font-size: 14px;
}
.view img {
    width: 868px;
    margin: 0 auto;
}
.view>>>p {
    /* display: inline-block; */
    /* margin-bottom: 100px; */
    padding-bottom: 10px;
}
</style>



